.buttonLine {
    position: relative;
    font-size: 16px;
    color: chocolate;
}

.myTopRightLink {
    position: absolute;
    top: -70px;
    right: 0;
    z-index: 110;
    font-weight: bold;
}

.myTopRightLink:hover {
    border-bottom: 1px solid chocolate;
}

.playTitle {
    color: #fe8800;
    font-size: 36pt;
    font-weight: bold;
    margin: 10px 0 5px 0;
}

.playerBox {
    padding: 10px 5px;
    border-radius: 10px;
    background-color: #282c34;
}

.videoInfoLine {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 10pt;
    padding: 5px;
}

.videoInfoLeft {
    flex: 6;
}

.videoInfoRight {
    text-align: right;
    flex: 6;
}